/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
body {
    line-height: 1;
}
ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
:root {
    --main-color: #1bac0b;
    --translucid-main-color: #1bac0b10;
    --background: #fffdff;
    --background-faded: #f3f4f6;
    --background-faded-hover: #f3f4f6;
    --text-color: #363735;
    --title-color: #1b1b1a;
}
[data-theme="light"] {
    --background: #fffdff;
    --background-faded: #f3f4f6;
    --background-faded-hover: #f3f4f6;
    --text-color: #363735;
    --title-color: #1b1b1a;
}
[data-theme="dark"] {
    --background: #202124;
    --background-faded: #303134;
    --background-faded-hover: #3d3f42;
    --text-color: #bdc1c6;
    --title-color: #e8eaed;
}

* {
    box-sizing: border-box;
    text-decoration: 0;
    border: none;
    outline: none;
    list-style: none;
    font-family: "Montserrat", sans-serif;
    scroll-behavior: smooth;
    transition: background-color 0.5s, color 0.5s;
}
